/* eslint-disable no-plusplus */
/* eslint-disable camelcase */
import { toBelarusian } from './language';

export default function (offsetSec) {
  const a = document.querySelector('#select-lang').options.selectedIndex;
  let typeLang = document.querySelector('#select-lang').options[a].innerText.toLowerCase();
  const typeLang2 = document.querySelector('#select-lang').options[a].innerText.toLowerCase();
  if (typeLang === 'be') {
    typeLang = 'en';
  }

  const date = new Date();

  date.setSeconds(date.getSeconds() + offsetSec + date.getTimezoneOffset() * 60);
  const objDate = {};

  /* day */
  let dayShort = date.toLocaleString(typeLang, { weekday: 'long' });
  if (typeLang2 === 'be') {
    dayShort = toBelarusian(dayShort);
  }
  objDate.day = dayShort.substr(0, 3);

  /* date */
  objDate.date = date.getDate();

  /* month */
  objDate.month = date.toLocaleString(typeLang, { month: 'long' });
  if (typeLang2 === 'be') {
    objDate.month = toBelarusian(objDate.month);
  }
  /* hours */
  objDate.hours = (`0${date.getHours()}`).slice(-2);

  /* minutes */
  objDate.minutes = (`0${date.getMinutes()}`).slice(-2);

  /* time of the year */
  let timeOfYear;
  if (date.getMonth() === 0 || 1 || 2) {
    timeOfYear = ['winter', 'summer'];
  } else if (date.getMonth() === 3 || 4 || 5) {
    timeOfYear = ['spring', 'autumn'];
  } else if (date.getMonth() === 6 || 7 || 8) {
    timeOfYear = ['summer', 'winter'];
  } else {
    timeOfYear = ['autumn', 'spring'];
  }
  objDate.timeOfYear = timeOfYear;


  // add 3 next day
  objDate.dayNext = [];
  for (let i = 0; i < 3; i++) {
    date.setDate(date.getDate() + 1);
    objDate.dayNext[i] = date.toLocaleString(typeLang, { weekday: 'long' });
    if (typeLang2 === 'be') {
      objDate.dayNext[i] = toBelarusian(objDate.dayNext[i]);
    }
  }

  return objDate;
}
