/* eslint-disable no-undef */


function initMap(lng, lat) {
  mapboxgl.accessToken = 'pk.eyJ1IjoieXVyaS05IiwiYSI6ImNrM3ZqM3d5aDA3OWMzZW9veDh0ZGN3MzYifQ.xxTlBOPgGA9r2UIxaHgSmg';
  const map = new mapboxgl.Map({
    container: 'map',
    style: 'mapbox://styles/mapbox/streets-v11',
    center: [lng, lat],
    zoom: 11,
  });
  return map;
}

async function moveMap(map, lat, lng) {
  map.flyTo({
    center: [lng, lat],
    zoom: 11,
  });
}

export { initMap, moveMap };
