/* eslint-disable camelcase */
/* eslint-disable no-plusplus */
import { getWeatherForecast } from './API';

async function initWeather(lat, lng) {
  const forecast = await getWeatherForecast(`lat=${lat}&lon=${lng}`);
  // console.log(forecast);

  const { description, icon, main } = forecast.list[0].weather[0];
  const {
    temp, humidity, pressure, feels_like,
  } = forecast.list[0].main;
  const { speed } = forecast.list[0].wind;
  const { pod } = forecast.list[0].sys;
  const pod1 = (pod === 'd' ? 'day' : 'night');

  const objWeather = {
    description,
    icon,
    temp,
    humidity,
    pressure,
    feelsLike: feels_like,
    speed,
    timeOfDay: pod1,
    main,
  };

  // 3 day weather forecast
  const indexUTCMidnight = forecast.list.findIndex((item) => (item.dt_txt.includes('00:00:00')));
  const indexTownMidday = indexUTCMidnight + 4 - Math.round(forecast.city.timezone / 3600 / 3);
  const list = [];
  for (let i = 0; i < 3; i++) {
    list[i] = forecast.list[indexTownMidday + i * 8];
  }

  objWeather.next3Data = list;

  return objWeather;
}

function logT() {
}
export { initWeather, logT };
