
function getImage(timeOfYear, timeOfDay, main) {
  return fetch(`https://api.unsplash.com/photos/random?query=${timeOfYear}+${timeOfDay}+${main}&client_id=7b76a47280b1e328889c12e58cfac9f74a7b39257fe1b4d8a8a4ddb681e74211`).then((res) => {
    if (res.status !== 200) {
      console.log('Number of requests image exceeded');
      return;
    }
    // eslint-disable-next-line consistent-return
    return res.json();
  });
}

let memory = {};
let image;

export default async function loadImage(timeOfYear, timeOfDay, main) {
  if (timeOfYear) {
    memory = {
      timeOfYear,
      timeOfDay,
      main,
    };
    image = await getImage(timeOfYear, timeOfDay, main);
  } else {
    image = await getImage(memory.timeOfYear, memory.timeOfDay, memory.main);
  }
  const block = document.querySelector('.wrapper_img');
  if (!image) {
    block.style.backgroundImage = 'url(https://images.unsplash.com/photo-1482542319871-7200a336e779?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=400&fit=max&ixid=eyJhcHBfaWQiOjEwMTYzNX0)';
  } else {
    block.style.backgroundImage = `url(${image.urls.small})`;
  }
}
