
export default function () {
  const pageContainer = `
  <div class="wrapper_img">
  <div class="wrapper">
    <div class="header">
      <ul class="controls">
        <li class="controls--btn controls--refresh">
          <img class="controls--img" src="./images/refresh.png" alt="">
        </li>
        <li class="controls--select">
          <select class="controls--select2" id="select-lang" name="cap" autofocus="">
            <option selected="selected">EN</option>
            <option>RU</option>
            <option>BE</option>
          </select>
        </li>
        <li class="controls--btn controls--temp_C temp">
        &degC
        </li>
        <li class="controls--btn controls--temp_F temp">
        &degF
        </li>
      </ul>
      <form action="#" class="search">
      <p class="search--alert"></p>
        <input type="search" id="search" placeholder="Search city" class="search--item"/>
        <label for="search" class="search--btn">SEARCH</label>
      </form>
    </div>
    <div class="main">
      <div class="weather">
        <div class="weather_today">
        </div>
        <div class="weather_3day">
        </div>
      </div>
      <div class="map">
        <div class="map--item" id = "map"></div>
        <p class="map--coord map--latitude">
            Latitude:
        </p>
        <p class="map--coord map--longitude">
            Longitude:
        </p>
      </div>
    </div>
  </div>
  </div>
`;

  document.querySelector('body').insertAdjacentHTML('afterbegin', pageContainer);
}
