/* eslint-disable no-plusplus */
import changeToDegree from './changeToDegree';
import { en, ru, be } from './language';
import { discriptionBE } from './languageBE';


export default function (weather, date, objCoor) {
  const indexLang = document.querySelector('#select-lang').options.selectedIndex;
  let lang;
  let weatherDescriptionLang = weather.description;
  if (indexLang === 0) {
    lang = en;
  } else if (indexLang === 1) {
    lang = ru;
  } else if (indexLang === 2) {
    lang = be;
    weatherDescriptionLang = discriptionBE[weather.description];
  }
  function toUpFirstLetter(word) {
    return `${word[0].toUpperCase()}${word.slice(1)}`;
  }

  /* draw today weather */
  const todayWeatherContainer = `
    <p class="weather_today--place">${objCoor.city}, ${objCoor.country}</p>
    <p class="weather_today--date"> ${toUpFirstLetter(date.day)} ${date.date} ${toUpFirstLetter(date.month)}  ${date.hours}:${date.minutes}</p>
    <p class="weather_today--temp">${Math.round(weather.temp)}</p>
    <div class="weather_today--icon-container">
      <img class="icon" src="https://openweathermap.org/img/wn/${weather.icon}@2x.png">
    </div>
    <div class="weather_today--info">
    <p>${weatherDescriptionLang.toUpperCase()}</p>
    <p>${lang.feelsLike}: <span class="weather_today--feels">${Math.round(weather.feelsLike)}</span>&deg</p>
    <p>${lang.humidity}: ${weather.humidity} %</p>
    <p>${lang.wind}: ${Math.round(weather.speed)} m/s</p>
    <p>${lang.pressure}: ${Math.round(weather.pressure)} mm</p>
    </div>
    `;

  const container = document.querySelector('.weather_today');
  while (container.firstChild) {
    container.removeChild(container.firstChild);
  }

  document.querySelector('.weather_today').insertAdjacentHTML('beforeend', todayWeatherContainer);

  /*  draw next 3 day weather */
  let day3WeatherContainer = '';
  for (let i = 0; i < 3; i++) {
    day3WeatherContainer += `
    <div class="weather_3day--item">
    <p>${toUpFirstLetter(date.dayNext[i])}</p>
    <p class="weather_3day--temp">${Math.round(weather.next3Data[i].main.temp)}</p>
    <div class="weather_3day--icon-container">
      <img class="icon" src="https://openweathermap.org/img/wn/${weather.next3Data[i].weather[0].icon}@2x.png">
    </div>
  </div>`;
  }

  const container3day = document.querySelector('.weather_3day');
  while (container3day.firstChild) {
    container3day.removeChild(container3day.firstChild);
  }

  document.querySelector('.weather_3day').insertAdjacentHTML('beforeend', day3WeatherContainer);


  /* draw coordinat */
  const latitude = document.querySelector('.map--latitude');
  latitude.innerText = `${lang.latitude}: ${changeToDegree(objCoor.lat)}`;

  const longitude = document.querySelector('.map--longitude');
  longitude.innerText = `${lang.longitude}: ${changeToDegree(objCoor.lng)}`;

  /* search form */
  const search = document.querySelector('.search--btn');
  search.innerText = `${lang.search}`;

  document.querySelector('.search--alert').innerText = `${lang.searchAlert}`;
}
