function toBelarusian(monthEn) {
  const vocabulary = {
    January: 'Студзень',
    February: 'Люты',
    March: 'Сакавiк',
    April: 'Красавiк',
    May: 'Май',
    June: 'Червень',
    July: 'Лiпень',
    August: 'Жнiвень',
    September: 'Верасень',
    October: 'Кастрычнiк',
    November: 'Лiстапад',
    December: 'Снежань',
    Monday: 'Панядзелак',
    Tuesday: 'Аўторак',
    Wednesday: 'Серада',
    Thursday: 'Чацьвер',
    Friday: 'Пятніца',
    Saturday: 'Сыбота',
    Sunday: 'Нядзеля',
  };
  return vocabulary[monthEn];
}

