function getGeoCoor(namePlace) {
  const a = document.querySelector('#select-lang').options.selectedIndex;
  const typeLang = document.querySelector('#select-lang').options[a].innerText.toLowerCase();

  return fetch(`https://api.opencagedata.com/geocode/v1/json?q=${namePlace}&key=44b11e6afc0642b3aff01b4b2a080437&language=${typeLang}`).then((res) => res.json());
}

function getUserLocation() {
  return fetch('https://ipinfo.io/json?token=1513b741ca8484').then((response) => response.json());
}

function getWeatherForecast(locIn) {
  const a = document.querySelector('#select-lang').options.selectedIndex;
  const typeLang = document.querySelector('#select-lang').options[a].innerText.toLowerCase();
  const tempType = document.querySelector('.controls--temp_C').classList.contains('select') ? 'Metric' : 'Imperial';
  return fetch(`https://api.openweathermap.org/data/2.5/forecast?${locIn}&lang=${typeLang}&units=${tempType}&APPID=24120eb4d8c2dc0f04ddddd17bf5214a`).then((response) => response.json());
}
export { getGeoCoor, getUserLocation, getWeatherForecast };
