/* eslint-disable camelcase */
import { getGeoCoor } from './API';

export default async function getGeoLocation(valueInput) {
  const loc = await getGeoCoor(valueInput);
  console.log(loc);
  if (loc.results.length === 0) {
    return;
  }
  const { lat, lng } = loc.results[0].geometry;
  const { offset_sec } = loc.results[0].annotations.timezone;

  const {
    city, state, country, county,
  } = loc.results[0].components;
  const objCoor = {
    lat,
    lng,
    offsetSec: offset_sec,
    city: city || county || state || ' ',
    country,
  };
  // eslint-disable-next-line consistent-return
  return objCoor;
}
