/* eslint-disable no-plusplus */
/* eslint-disable camelcase */
import initPage from './js/initPage';
import { initWeather } from './js/getWeather';
import loadImage from './js/loadImage';
import './img/refresh.png';
import { initMap, moveMap } from './js/getMap';
import './css/style.css';
import getTimePlace from './js/getTimePlace';
import { getUserLocation } from './js/API';
import renderWeatherInfo from './js/reflowPage';
import getGeoLocation from './js/getGeoLocation';

initPage();

let mapR = {};
let cityGlb;
let offsetSecGlb;
/* from localStorage */
document.querySelector('#select-lang').options.selectedIndex = localStorage.getItem('lang');
if (localStorage.getItem('temp') === 'F') {
  document.querySelector('.controls--temp_F').classList.add('select');
} else {
  document.querySelector('.controls--temp_C').classList.add('select');
}

async function initFirstContent() {
  const location = await getUserLocation();
  const { loc, city } = location;
  const arrLoc = loc.split(',');
  const objCoor = {
    lat: arrLoc[0],
    lng: arrLoc[1],
    city,
  };
  /* get name of city and country any language */
  const objCoorGeo = await getGeoLocation(objCoor.city);
  objCoor.city = objCoorGeo.city;
  objCoor.country = objCoorGeo.country;

  const timezoneOffset = (new Date()).getTimezoneOffset() * 60;
  const objDate = getTimePlace(-timezoneOffset);
  const objWeather = await initWeather(objCoor.lat, objCoor.lng, -timezoneOffset);
  renderWeatherInfo(objWeather, objDate, objCoor);
  mapR = initMap(objCoor.lng, objCoor.lat);

  // getting the time of year and time of day
  const timeOfYear = (objCoor.lat > 0) ? objDate.timeOfYear[0] : objDate.timeOfYear[1];
  loadImage(timeOfYear, objWeather.timeOfDay, objWeather.main);

  cityGlb = objCoor.city;
  offsetSecGlb = -timezoneOffset;
}

initFirstContent();


async function initNewContent() {
  const valueInput = document.querySelector('#search').value;
  if (valueInput === '') {
    return;
  }

  const objCoor = await getGeoLocation(valueInput);
  if (!objCoor) {
    const elemAlert = document.querySelector('.search--alert');
    elemAlert.style.display = 'block';
    return;
  }
  cityGlb = objCoor.city;
  const objDate = getTimePlace(objCoor.offsetSec);

  moveMap(mapR, objCoor.lat, objCoor.lng);
  const objWeather = await initWeather(objCoor.lat, objCoor.lng, objCoor.offsetSec);
  renderWeatherInfo(objWeather, objDate, objCoor);

  /* getting the time of year, time of day, */
  const timeOfYear = (objCoor.lat > 0) ? objDate.timeOfYear[0] : objDate.timeOfYear[1];
  loadImage(timeOfYear, objWeather.timeOfDay, objWeather.main);

  offsetSecGlb = objCoor.offsetSec;
}

/* change language */
async function changeLanguage() {
  document.querySelector('#select-lang').classList.remove('select2');
  const objCoor = await getGeoLocation(cityGlb);
  const objDate = getTimePlace(objCoor.offsetSec);
  const objWeather = await initWeather(objCoor.lat, objCoor.lng, objCoor.offsetSec);
  renderWeatherInfo(objWeather, objDate, objCoor);
}

const langChange = document.querySelector('#select-lang');
langChange.onchange = changeLanguage;
langChange.onblur = () => document.querySelector('#select-lang').classList.remove('select2');

/* bottom change language */
document.querySelector('.controls--select').addEventListener('mousedown', () => {
  document.querySelector('#select-lang').classList.add('select2');
});

// search botton
document.querySelector('.search--btn').addEventListener('click', () => { initNewContent(); });
document.addEventListener('keydown', (event) => {
  if (event.code === 'Enter' || event.code === 'NumpadEnter') {
    initNewContent();
  }
});

/* refresh botton */
document.querySelector('.controls--refresh').addEventListener('mousedown', () => {
  loadImage();
  document.querySelector('.controls--refresh').classList.add('select');
});
document.querySelector('.controls--refresh').addEventListener('mouseup', () => {
  document.querySelector('.controls--refresh').classList.remove('select');
});

/* change temperature */
function changeTemp(event) {
  let tempValue = document.querySelector('.weather_today--temp').innerText;
  tempValue = event.target.className.includes('F') ? Math.round((tempValue * 1.8) + 32) : Math.round((tempValue - 32) / 1.8);
  document.querySelector('.weather_today--temp').innerText = tempValue;

  let tempValueFell = document.querySelector('.weather_today--feels').innerText;
  tempValueFell = event.target.className.includes('F') ? Math.round((tempValueFell * 1.8) + 32) : Math.round((tempValueFell - 32) / 1.8);
  document.querySelector('.weather_today--feels').innerText = tempValueFell;

  for (let i = 0; i < 3; i++) {
    tempValue = document.querySelectorAll('.weather_3day--temp')[i].innerText;
    tempValue = event.target.className.includes('F') ? Math.round((tempValue * 1.8) + 32) : Math.round((tempValue - 32) / 1.8);
    document.querySelectorAll('.weather_3day--temp')[i].innerText = tempValue;
  }
}

/* click temp botton */
document.querySelector('.controls').addEventListener('mousedown', (event) => {
  if ((event.target.classList.contains('temp')) && !event.target.classList.contains('select')) {
    document.querySelectorAll('.temp').forEach((btn) => btn.classList.remove('select'));
    event.target.classList.add('select');
    changeTemp(event);
  }
});

/* localStorage */
window.addEventListener('unload', () => {
  const lang = document.querySelector('#select-lang').options.selectedIndex;
  localStorage.setItem('lang', lang);
  const temp = document.querySelector('.controls--temp_C').classList.contains('select') ? 'C' : 'F';
  localStorage.setItem('temp', temp);
});

/* Update time */
function updateTime() {
  if (document.querySelector('.weather_today--date')) {
    const objDate = getTimePlace(offsetSecGlb);
    document.querySelector('.weather_today--date').innerText = `${objDate.day} ${objDate.date} ${objDate.month}    ${objDate.hours}:${objDate.minutes}`;
  }
}
setInterval(updateTime, 30000);


/* search alert */
document.querySelector('.search--item').addEventListener('mousedown', () => {
  document.querySelector('.search--alert').style.display = 'none';
});
document.addEventListener('keydown', () => {
  document.querySelector('.search--alert').style.display = 'none';
});
